@import url(https://fonts.googleapis.com/css?family=Lobster);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
    margin: 0;
    padding: 0;
}
.cont {
    display: flex;
    justify-content: space-evenly;
}

.cities {
    border: 2px solid rgb(0, 119, 255);
    padding: 11px;
    border-radius: 10px;
    background-color: rgb(0, 119, 255);
    color: white;
    font-weight: bold;
    margin-top: 2vh;
}

.cities:hover {
    background-color: rgb(248, 120, 0);
    border: rgb(248, 120, 0);
    transform: scale(1.1);
    cursor: pointer;
}

.menu {
    margin-top: -4vh;
    height: 92vh;
    width: auto;
    background-image: url(/voyagesid/static/media/photo-1487349384428-12b47aca925e.465ea85a.jpeg);
    background-size: 100% 110%;
    justify-content: space-evenly;

}

.BigTitle {
    margin-left: 7vw;
}

.Title {
    display: flex;
    justify-content: center;
    text-align: center;
    font-family: 'Lobster';
}

.followUs {
    font-family: 'Lobster';
    margin-top: 4vh;
    font-size: 2vw;
}

.VID {
    margin-top: 4vh;
    margin-bottom: -4vh;
    padding-bottom: -3vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Lobster';
    font-size: 4vw;
    margin-left: 2vw;
}

.PRS {
    background-image: url(/voyagesid/static/media/beach.8d19350b.jpg);
    background-size: cover;
    display: flex;
    justify-content: center;
    height: 100vh;
    align-items: center;
}

.contLink {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-direction: column;
}

.link {
    max-width: 100%;
    max-height: 100%;
    width: 50px;
    height: 50px;
    background-image: url("/voyagesid/static/media/instagram (1).26631a40.png");
    background-size: cover;
    color: transparent;
}

@media screen and (max-width: 500px) {
    .menu {
        height: 92vh;
        width: auto;
        background-image: url(/voyagesid/static/media/photo-1487349384428-12b47aca925e.465ea85a.jpeg);
        background-size: cover;
    }
    .BigTitle {
        font-size: 11vw;
    }
    .Title {
        font-size: 5vw;
    }
    .followUs {
        font-family: 'Lobster';
        margin-top: 4vh;
        font-size: 4vw;
    }
}

@media screen and (min-width: 1000px) {
        .contLink{
            margin-top: -13vh;
        }
    }

.card {
    width:280px;
    display: block;
    margin: 60px auto;
    /* box-shadow: 10px 5px 40px 20px darken(#341cac, 5%); */
    transition: .25s;
    margin-top: 10vh;

}
    .card:hover {
      box-shadow: 10px 5px 40px 20px darken(#341cac, 10%);
      /* cursor: pointer; */

    }
      .button-primary {
        transform: translate(10px, 0);
      }
  
  .card-header {
    height: 30vh;
    width: 100%;
    width:100%;
    background-size:cover;
    background-position: center;
    color:#fff;
  }
  
  .card-header--title {
    text-transform: uppercase;
    margin: 0;
    font-family: 'Lobster';
    font-weight: 200;
  }

  
  .city {
    padding-top: 2vh;
    font-size: 20px;
    display: flex;
    text-align: center;
    justify-content: center;
}


  .card-body {
    /* padding: 15px; */
    margin-top: -3vh;
    /* background-color: transparent; */
    width: 100%;
    background-color: white;
}
    
  
  .date {
    font-size: 11px;
    font-weight: 700;
    color: grey;
    display: flex;
    justify-content: center;
}

.prix {
    font-size: 14px;
    font-weight: bold;
    color: black;
    display: flex;
    justify-content: center;
    padding-bottom: 2vh;
}
  
  .body-content {
    padding-bottom: 40px;
    font-size: 13px;
    line-height: 1.8;
  }

  .linkToSky{
    border: 2px solid rgb(0, 119, 255);
    padding: 11px;
    /* border-radius: 10px; */
    background-color: rgb(0, 119, 255);
    color: white;
    font-weight: bold;
    margin-top: -1vh;
    display: flex;
    justify-content: center;
}

  
  .button-primary {
    border: none;
    box-shadow: none;
    font-family: inherit;
    background-color:transparent;
    color: blue;
    font-size: 15px;
    transition:.25s;
    }
    i{
      padding-right:4px;
    }

    @media screen and (min-width:1000px) {
      .city {
        margin-top:-5vh;
        font-size: 20px;
      }
    }

.container {
    height: 10vh;
    width: 100%;
    background-image: url(/voyagesid/static/media/photo-1549849171-09f62448709e.0f531127.jpeg);
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.voyagesID {
    color: white;
    margin-left: 2vw;
    font-family: 'Lobster';
}

.airplane {
    background-image: url(data:image/png;base64,);
    object-fit: contain;
    height: 5vh;
    width: 10vw;
    background-repeat: no-repeat;
    margin-left: 1vw;
}

.planeAnimation {
    background-image: url(data:image/png;base64,);
    height: 9vh;
    width: 5vw;
    background-repeat: no-repeat;
    background-size: contain;       
    -webkit-animation-duration: 30s;       
            animation-duration: 30s;
    -webkit-animation-name: slidein;
            animation-name: slidein;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}


@-webkit-keyframes slidein {
    0% {
      margin-left: 0%;
    }
  
    100% {
      margin-left: 75vw;
    }
  }


@keyframes slidein {
    0% {
      margin-left: 0%;
    }
  
    100% {
      margin-left: 75vw;
    }
  }

@media screen and (max-width:500px) {
    @-webkit-keyframes slidein {
        0% {
          margin-left: 0%;
        }
      
        100% {
          margin-left: 50vw;
        }
      }
    @keyframes slidein {
        0% {
          margin-left: 0%;
        }
      
        100% {
          margin-left: 50vw;
        }
      }
      .planeAnimation {
        background-image: url(data:image/png;base64,);
        height: 7vh;
        width: 14vw;
        background-repeat: no-repeat;
        background-size: cover;   
        -webkit-animation-duration: 10s;   
                animation-duration: 10s;
        -webkit-animation-name: slidein;
                animation-name: slidein;
        -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite;
    }
}
.Modal {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    text-align: center;
    padding: 2em;
    min-width: 20em;
    max-width: 70%;
    color: black;
    background-color: #fff;
    border-radius: 1em;
    transform: translate(-50%, -50%);
    outline: transparent;
    font-family: "lobster";
  }
  .Overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,.4);
  }

  .Back {
      background-image: url(/voyagesid/static/media/Stockholm.bd7e2a39.jpg);
      background-size: cover;
      border-radius: 2vh;
  }
